.restart {
  color: #fff;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: #7873f5;
  border: none;
  border-radius: 8px;
  margin: 0 auto 30px;
  padding: 12px 30px;
  font-size: 18px;
  transition: background-color .3s;
  display: block;
}

.restart:disabled {
  cursor: none;
  background-color: #ccc;
}

.animation {
  background: linear-gradient(270deg, #ff6ec4, #7873f5, #ff6ec4) 0 0 / 600% 600%;
  border-radius: 15px;
  width: 120px;
  height: 30px;
  margin: 0 auto 30px;
  animation: 3s linear infinite waveMove;
  display: none;
  box-shadow: 0 0 10px #ff6ec4, 0 0 20px #7873f5;
}

@keyframes waveMove {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}
